import { findIndex, includes, isEqual, uniq } from 'lodash';

// ---------------------------------------- Globals //
const mouseEvents = [
	'click', // Works with multiple single clicks pattern
	'dblclick', // Only works with single double click pattern set
	'mouseup', // Works with multiple mouseup clicks pattern
	'mousedown', // Works with multiple mousedown clicks pattern
];
const easterEggsTriggerEggs = [];
let $this = null;

const EasterEggTriggerCore = {
	// ---------------------------------------- Defaults //
	pluginOptions: {
		callback: null,
		delay: 500,
		destroyBus: false,
		name: 'easter-egg',
		pattern: ['ArrowUp', 'ArrowUp', 'ArrowDown', 'ArrowDown', 'ArrowLeft', 'ArrowRight', 'ArrowLeft', 'ArrowRight', 'b', 'a'],
		target: 'div',
		triggered: null,
		type: 'keydown',
		withBus: true,
	},
	input: [],
	methodOptions: {},
	options: {},
	targets: {
		nodes: [],
		ids: [],
		classNames: [],
	},
	timeout: () => { },

	/*
		---------------------------------------- Init
		? Initializes the plugin
	*/
	init(globalOptions, methodOptions) {
		console.group('EasterEggTriggerCore', '------------------------------- Core Init');
		console.log({ globalOptions, methodOptions });

		// Merge user and global //
		this.pluginOptions = { ...this.pluginOptions, ...globalOptions, ...methodOptions };
		$this = this;

		console.log(this.pluginOptions);
		this.layEggs();
	},

	/*
		---------------------------------------- Lay Eggs
		? Adds Eggs to Easter Eggs
	*/
	layEggs() {
		easterEggsTriggerEggs.push(this.pluginOptions);
		console.log('layEggs', { easterEggsTriggerEggs });

		this.collectEggs();
	},

	/*
		---------------------------------------- Collect Eggs
		? Add eggs to global easterEggsTriggerEggs array
	*/
	// Loop through eggs and call add event listener //
	collectEggs() {
		console.log(easterEggsTriggerEggs.length);
		Object.values(easterEggsTriggerEggs).forEach((egg) => {
			console.log({ egg });
			const newEgg = egg;

			if (!newEgg.pattern) {
				newEgg.pattern = this.pluginOptions.pattern;
			}

			if (!newEgg.target) {
				newEgg.target = this.pluginOptions.target;
			}
		});

		this.addListener();
	},

	/*
		---------------------------------------- Add Listener
		? Create and add event listener
	*/
	addListener() {
		console.log('addListener', this.pluginOptions.type);

		document.addEventListener(this.pluginOptions.type, this.capturePattern, false);
	},

	/*
		---------------------------------------- Capture Pattern
		? Capture the Keys or Click Pattern
	*/
	capturePattern(e) {
		console.log('capturePattern', { e });
		console.log($this);
		const key = '';
		// console.log({ e });

		if ($this.timeout !== null) {
			clearTimeout($this.timeout);
		}

		// -------------------- Keyboard Events //
		if (e.key !== undefined) {
			key = e.key;
		}

		// // -------------------- Mouse Events //
		// if (_.includes(EasterEggTrigger.mouseEvents, e.type)) {
		// 	console.log('includes');
		// 	const target = e.target;
		// 	key = e.type;

		// 	EasterEggTrigger.targets.nodes.push(target.nodeName.toLowerCase());
		// 	EasterEggTrigger.targets.ids.push(target.id);
		// 	EasterEggTrigger.targets.classNames.push(target.classList.value);
		// }

		// console.log({ key });

		// // ! Inputs is being pushed a dblclick as ['click', 'click', 'dblclick'] //
		// EasterEggTrigger.input.push(key);
		// EasterEggTrigger.checkPattern(e);

		console.log({ key });

		$this.hatched();
		return false;
	},

	/*
	 ---------------------------------------- Hatch
	? Easter Egg Hatched
	*/
	hatched() {
		console.log('HATCHED');
		console.groupEnd('EasterEggTriggerCore');
	},
};

export default EasterEggTriggerCore;
